          @import '@/assets/sass/mixins.scss';
        

@import '@/assets/sass/variables';

.legend {
  position: absolute;
  z-index: 6;
  top: 1.25rem;
  right: 1.25rem;
  max-width: 400px;

  .legend-button {
    position: static;

    button {
      width: auto;
      padding: 0 0.5em;
    }
  }

  .legend-content {
    margin-top: 0.5rem;
    margin-left: 1.25rem;
    border-radius: 2px;
    border: 1px solid lightgray;
    padding: 0.5rem;
    background: white;
    clear: both;
  }

  .legend-item + .legend-item {
    margin-top: 2rem;
    padding-top: 1em;
    border-top: solid 1px $grey-lighter;
  }
}

.icon {
  position: absolute;
  top: 7px;
  right: -10px;
}
.icon-notification {
  color: $primary;
}

@media screen and (max-width: $tablet) {
  .legend {
    top: 0.5rem;

    .legend-content {
      margin-left: 0.5rem;
    }
  }
}
