
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

$section-padding: 0.75rem; //TODO find this variable
$box-padding: 1.25rem; //TODO find this variable
$header-height: 34px;
$box-margin: 1.5rem; //TODO find this variable
$yeti-height: calc(100vh - #{$navbar-height});
$yeti-height-ad: calc(100vh - #{$navbarad-height});

.yeti-app {
  position: relative;
  padding: $section-padding;
}

.yeti-icon {
  transform: scale(0.9);
}

.yeti-title {
  margin-top: calc(0.75rem + 2px);
}

@media screen and (max-width: $tablet) {
  .mobile-result-map {
    margin-top: 0;
    height: $yeti-height;

    .documents-container {
      display: None;
    }
  }

  .mobile-result-card {
    .map-container {
      visibility: hidden; // map does not like to be in a display none...
    }
  }
}

@media screen and (min-width: $tablet) {
  .yeti-content {
    height: $yeti-height;

    .form-container {
      max-width: 450px;
      height: calc(100% - 0.75rem);
      overflow: auto;
    }
  }
}

@media screen and (min-width: $widescreen) {
  .yeti-content {
    height: $yeti-height-ad;
  }
}

.yeti-validation--top {
  position: absolute;
  right: 0;
  top: 0.75rem;
  width: 55%;
  z-index: 1;
}

.yeti-validation--bottom {
  margin-top: 1rem;
}

.yeti-logos {
  box-shadow: none;
  background: none;
  mix-blend-mode: multiply;

  img {
    max-width: 65%;
  }
}

.yeti-overlay {
  position: absolute;
  z-index: 10;
  top: 0.75rem;
  right: 0;
  left: 0;
  min-height: 100%;

  & ~ .yeti-content {
    visibility: hidden;
  }
}
