          @import '@/assets/sass/mixins.scss';
        

@import '@/assets/sass/variables';

.footer {
  background: $grey-lighter;
  margin: 10px -5px -5px;
}
.overlay-icons {
  display: flex;
  justify-content: center;
  margin: 10px;
}
.overlay-icons a {
  margin: 0 5px;
  font-size: 0.9em;
  line-height: 1;
}
.overlay-icons img {
  border: solid 1px rgba($black, 0.5);
}
.overlay-icons-imgs {
  position: relative;
}
.overlay-link-icon {
  position: absolute;
  top: 1px;
  right: 1px;
  background: white;
  padding: 2px;
}
